'use client';
import { useAuth } from '@/contexts/AuthContext';
import DashboardCard from '@/app/components/DashboardCard';

export default function HomePage() {
  const { user } = useAuth();

  if (!user) {
    return (
      <main className="flex-grow flex flex-col items-center justify-center p-8 text-center">
        <h1 className="text-4xl font-bold mb-4">Bienvenido a la Veterinaria</h1>
        <p className="text-lg">Por favor, inicia sesión para gestionar tus mascotas y turnos.</p>
      </main>
    );
  }

  return (
    <main className="flex-grow p-8">
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
        <DashboardCard title="Turno Consulta" href="/turnos/consulta" icon="/file.svg" />
        <DashboardCard title="Turno Peluquería" href="/turnos/peluqueria" icon="/window.svg" />
        <DashboardCard title="Mis Turnos" href="/turnos" icon="/globe.svg" />
        <DashboardCard title="Mis Mascotas" href="/mascotas" icon="/file.svg" />
        <DashboardCard title="Tienda" href="/tienda" icon="/window.svg" />
        <DashboardCard title="Adopciones" href="/adopciones" icon="/globe.svg" />
      </div>
    </main>
  );
}
